import React from "react"
import Bio from "../components/bio"
import Projects from "../components/projects"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { graphql, useStaticQuery } from "gatsby"
import { Helmet } from "react-helmet"
import Pdf from "../static/resume.pdf"
import "../components/base.css"

const IndexPage = () => {
  const indexData = useStaticQuery(graphql`
    query HomeQuery {
      site {
        siteMetadata {
          title
          social {
            github
            linkedin
          }
        }
      }
    }
  `)
  // URL for GitHub
  const githubLink = `https://github.com/${indexData.site.siteMetadata.social.github}`
  // URL for LinkedIn
  const linkedinLink = `https://linkedin.com/in/${indexData.site.siteMetadata.social.linkedin}`
  return (
    <Layout id="index_layout" location="home">
      <SEO title="Andrew Stolzle" description="Personal portfolio of Andrew Stolzle" lang="en" />
      <Helmet title={"Home"} />
      <div className={"index-container"}>
        <div className={"index-title"}>Andrew Stolzle</div>
        <div className={"index-links"}>
          <div className="info-link">
            <a className="white-link" href={Pdf} target="_blank" rel="noopener noreferrer">
              Resume
            </a>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path
                d="M10 6H6C4.89543 6 4 6.89543 4 8V18C4 19.1046 4.89543 20 6 20H16C17.1046 20 18 19.1046 18 18V14M14 4H20M20 4V10M20 4L10 14"
                stroke="#ffffff"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </div>
          <div className="info-link">
            <a className="white-link" href={githubLink} target="_blank" rel="noopener noreferrer">
              Github
            </a>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path
                d="M10 6H6C4.89543 6 4 6.89543 4 8V18C4 19.1046 4.89543 20 6 20H16C17.1046 20 18 19.1046 18 18V14M14 4H20M20 4V10M20 4L10 14"
                stroke="#ffffff"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </div>
          <div className="info-link">
            <a className="white-link" href={linkedinLink} target="_blank" rel="noopener noreferrer">
              LinkedIn
            </a>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path
                d="M10 6H6C4.89543 6 4 6.89543 4 8V18C4 19.1046 4.89543 20 6 20H16C17.1046 20 18 19.1046 18 18V14M14 4H20M20 4V10M20 4L10 14"
                stroke="#ffffff"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </div>
          <div className="info-link">
            <a className="white-link" href={`mailto:andrewstolzle@gmail.com`}>
              Email
            </a>
            <svg width="24" height="24" fill="none" viewBox="0 0 24 24">
              <path
                stroke="#ffffff"
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z"
              />
            </svg>
          </div>
        </div>
        <Bio />
        <Projects />
      </div>
    </Layout>
  )
}

export default IndexPage
