import React from "react"
import { Link } from "gatsby"
import "./base.css"

const Projects = () => {
  const projects = [
    {
      id: 1,
      title: "MyMentor",
      subtitle: "Mobile app that connects students & professors",
      description: "Xamarin Forms frontend - .NET backend",
      url: "mymentor",
      code: "https://github.com/frederickbyte/MyMentor",
    },
    {
      id: 2,
      title: "HPC Budget Manager",
      subtitle: "Web application for Healing Place Church",
      description: "AngularJS frontend with Material UI and Kendo UI components",
      url: "hpcbudgeting",
      code: "https://andrewstolzle.com",
    },
    {
      id: 3,
      title: "Personal Site",
      subtitle: "My personal website",
      description: "React web app powered with Gatsby",
      url: "personalsite",
      code: "https://github.com/frederickbyte/frederickbyte.github.io",
    },
  ]
  return (
    <div className="flex-column">
      <h3 style={{ color: `rgb(247,140,108)`, fontSize: `1.5rem`, fontWeight: 700 }}>Recent Projects</h3>
      {projects.map(p => {
        return (
          <div className="card" key={p.id}>
            <div className="project-title">{p.title}</div>
            <div className="card-subtitle">{p.subtitle}</div>
            <div className="card-desc">{p.description}</div>
            <div style={{ display: `flex`, alignItems: `center` }}>
              <a href={p.code} rel="noopener noreferrer" className="btn btn-outline-green default-margin-right">
                CODE
              </a>
              <Link to={p.url} className="btn btn-outline-blue">
                INFO
              </Link>
            </div>
          </div>
        )
      })}
    </div>
  )
}

export default Projects
